<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
        <ctitle :title="'包案领导动态'"></ctitle>
        <!--<span class="c-sxr">包案领导：{{this.curName}}</span>-->
        <div class="w-echart" id="w-echart">
          <div ref="barEl">
            <horizontalBar :params="leftBarParams" @departmentClick="changeType"></horizontalBar>
          </div>
        </div>
      </div>
      <div class="w-right">
        <div class="c-top">
          <div class="part ct-left">
            <ctitle :title="'包案详情'"></ctitle>
            <div class="detail">
              <allCount class="all-count" :count="allcount"></allCount>
              <detailList :params="detailList"></detailList>
            </div>
          </div>
          <div class="part ct-center">
            <ctitle :title="'化解情况'"></ctitle>
            <div class="inner" v-loading="!hjqkPieParams.data.length"
                 :element-loading-background="$STANDARD.loadingBgColor">
              <div class="zbPie" v-if="hjqkPieParams.data.length">
                <div class="pie-label">
                  <p>已化解</p>
                  <p>信访件总数</p>
                </div>
                <div class="chart">
                  <div class="pie-percent" v-if="hjqkPieParams.data[0]">
                    {{hjqkPieParams.data[0].value*100}}%
                  </div>
                  <percentPie :params='hjqkPieParams'></percentPie>
                </div>
              </div>
            </div>
          </div>
          <div class="part ct-right">
            <ctitle :title="'满意度评价'"></ctitle>
            <div class="inner">
              <liquidfill :params='MydliquidParams'></liquidfill>
            </div>
          </div>
        </div>
        <div class="c-bottom">
          <div class="part cb-left">
            <ctitle :title="'问题分类'"></ctitle>
            <div class="inner">
              <pie-chart3D :params="wtflPieParams"></pie-chart3D>
            </div>
          </div>
          <div class="part cb-center">
            <ctitle :title="'信访形式'"></ctitle>
            <div class="inner">
              <pie-chart3D :params="xfxsPieParams"></pie-chart3D>
            </div>
          </div>
          <div class="part cb-right">
            <ctitle :title="'地区分布'"></ctitle>
            <div class="inner">
              <loopPie :params="dqfbPieParams"></loopPie>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import {gradientColor} from "@/utils/gradient";
  import ctitle from "@/components/title/index";
  import loopPie from "@/components/pie/LoopPie";
  import PieChart3D from "@/components/pie/PieChart3D";
  import BScroll from "better-scroll";
  import horizontalBar from "@/components/bar/HorizontalBar";
  import allCount from "@/components/list/allCount";
  import detailList from "@/components/list/detailList";
  import percentPie from "@/components/pie/percentPie";
  import liquidfill from "@/components/liquidfill/Liquidfill";
  export default {
    name: "sxr",
    components: {
      ctitle,
      loopPie,
      PieChart3D,
      horizontalBar,
      percentPie,
      allCount,
      detailList,
      liquidfill
    },
    data() {
      return {
        curName: "",
        listScroll: null,
        // 包案领导动态
        leftBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            click: true,
            highlightIndex: 0
          }
        },
        // 包案详情
        allcount: -1,
        detailList: null,
        // 化解情况
        hjqkPieParams: {
          data: [],
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options: {
            bgColor: {
              start: 'rgba(255,255,255,0.7)',
              end: 'rgba(255,255,255,0.1)'
            },
            GradientColor: {
              start: '#5cd8ff',
              end: '#5cd8ff'
            }
          },
          realRadius: ['45%', '55%'],
          placeHolderRadius: ['45%', '55%'],
        },
        // 满意度评价
        MydliquidParams:{
          data: [],
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options:{
            radius: "50%",
          }
        },
        //问题分类
        wtflPieParams: {
          data: [],
          color: ["#6f69ff", "#59eaff", "#74d650", "#f0ff87", "#ff965d", "#5fffb6", "#61c6ff", "#dda2ea", "#745cc5", "#ffc343", "#fb548f", "#a7f68d", "#6994ff"],
          angle: 20,
          radius: "30%",
          labelRadius: 15,
          depth3D: 20,
          styleObj: {
            width: "100%",
            height: "100%"
          }
        },
        // 信访形式
        xfxsPieParams: {
          data: [],
          color: ["#5166c1", "#32b1e0", "#15bbc2"],
          angle: 20,
          radius: "35%",
          labelRadius: 35,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
        },
        //地区分布
        dqfbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            colors: this.$STANDARD.pieMoreColors,
            title: "",
            radius: ["45%", "60%"],
            center: ["50%", "50%"],
            centerRadius: ["35%", "36%"],
            labelRich: {
              b: this.fontSize(0.16),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            borderColor: "#0B2244",
            bRichPadding: [30, -50, 0, -50],
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "20%", // label字号
            rollFlag: true
          }
        }
      };
    },
    mounted() {
      this.getLeftAPI(this.year);
    },
    methods: {
      getLeftAPI(year) {
        // 1包案领导动态（左侧柱图）
        get_http("gjzlLdblLeftBar", {year: year})
          .then(res => {
            this.leftBarParams.data = res.data.data.sort((a, b) => b.value - a.value);
            this.leftBarParams.options.highlightIndex = this.leftBarParams.data.length - 1;
            this.curName = this.leftBarParams.data[this.leftBarParams.data.length - 1].name;
            if (this.leftBarParams.data.length > 12) {
              this.computation();
            }
            this.getRightAPI(year, this.curName);
          })
          .catch(err => {
            console.log(err);
          });
      },
      getRightAPI(year, name) {
        // 2包案详情（总数）
        get_http("gjzlLdblBaxqTotal", {year: year, ld: name})
          .then(res => {
            this.allcount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.初信重信占比（各类型）
        get_http("gjzlLdblBaxqType", {year: year, ld: name})
          .then(res => {
            this.detailList = res.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4化解情况
        get_http("gjzlLdblHjqkPie", {year: year, ld: name})
          .then(res => {
            this.hjqkPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5满意度评价
        get_http("gjzlLdblMydliquid", {year: year, ld: name})
          .then(res => {
            this.MydliquidParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 6问题分类
        get_http("gjzlLdblWtflPie", {year: year, ld: name})
          .then(res => {
            this.wtflPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 7信访形式
        get_http("gjzlLdblXfxsPie", {year: year, ld: name})
          .then(res => {
            this.xfxsPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 8地区分布
        get_http("gjzlLdblDqfbPie", {year: year, ld: name})
          .then(res => {
            this.dqfbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 点击左侧柱图
      changeType(name) {
        // this.leftBarParams.data.filter((item, index) => {
        //   if (item.name === res.params.name) {
        //     this.curName = item.name;
        //   }
        // });
        this.curName = (name === '' ? 'drop' : name);
        this.getRightAPI(this.year, this.curName);
      },
      computation(data) {
        let height = (60 * this.leftBarParams.data.length) / 100 + "rem";
        $(this.$refs.barEl).css('height', height);
        this.initScroll();
      },
      // 初始化滚动条
      initScroll() {
        let listwraper = document.getElementById("w-echart");
        this.scrollFunc(this.listScroll, listwraper);

        // 更改滚动条样式
        $(".bscroll-vertical-scrollbar").css({
          width: ".1rem",
          background: "rgba(2, 144, 213, .2)"
        });
        $(".bscroll-vertical-scrollbar .bscroll-indicator").css({
          backgroundImage:
            "linear-gradient(to bottom, rgba(14, 149, 210, 0.8) , rgba(19, 240, 255, 0.8))",
          border: "none"
        });
        $(".bscroll-horizontal-scrollbar .bscroll-indicator").css({
          backgroundImage:
            "linear-gradient(to right, rgba(14, 149, 210, 0.8) , rgba(19, 240, 255, 0.8))",
          border: "none"
        });
      },
      scrollFunc(scrollName, scrollWrap) {
        if (!scrollName) {
          scrollName = new BScroll(scrollWrap, {
            bounce: false,
            momentumLimitDistance: 5,
            scrollX: true,
            scrollbar: {
              fade: false,
              interactive: false // 1.8.0 新增
            },
            mouseWheel: true
          });
        } else {
          // 复用Bscroll
          scrollName.refresh();
        }
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getLeftAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box

      .w-left
        width 3rem
        height 100%
        position relative
        font-size 0
        border-right 1px #91e3f7 dashed
        .c-sxr
          margin-left .4rem
          line-height .3rem
          font-size .16rem
          color #fff
          text-shadow 0 0 10px #276bf4
        .w-echart
          position relative
          width 100%
          height 90%
          overflow hidden
          > div
            height 100%
      .w-right
        display flex
        flex 1
        flex-direction column
        box-sizing border-box
        .part
          flex 1
          width 100%
          height 100%
          .inner
            display flex
            height calc(100% - 0.28rem)
            .chart
              width 100%
              height 100%
        .c-top
          flex 1
          display flex
          position relative
          .ct-left
            .detail
              height calc(100% - 0.28rem)
              display flex
              flex-direction column
              justify-content center
              align-items center
              .all-count
                height 0.48rem
                padding 0.2rem 0
          .ct-center
            .zbPie
              position relative
              flex 1
              width 100%
              display flex
              justify-content center
              align-items center
              .pie-label
                position relative
                left 50px
                width 110px
                text-align center
                font-weight bold
                p
                  color #fff
                  font-weight 600
                  font-size 18px
                  line-height 50px
                  &:nth-child(1)
                    width 110px
                    border-bottom 2px solid rgba(255, 255, 255, 0.7)
              .chart
                position relative
                flex 1
                width 100%
                height 100%
                .pie-percent
                  position absolute
                  top 50%
                  left 50%
                  color #e03562
                  transform translate(-50%, -50%)
                  font-size 32px
                  font-weight 700
        .c-bottom
          flex 1
          position relative
          display flex
</style>
